:root {
  --blue-color: #00acdc;
}

* {
  margin: 0;
}

body {
  width: 100%;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  padding: 0 50px;
  display: flex;
}

.header__logo_link {
  height: 40px;
}

.nav {
  justify-content: center;
  align-items: center;
  width: auto;
  height: auto;
  display: flex;
}

.nav__list {
  padding: 0;
  display: flex;
}

.nav__item {
  margin: 0 10px;
  list-style-type: none;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  text-decoration: none;
  display: flex;
}

.nav__link.is-active {
  color: var(--blue-color);
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: var(--blue-color);
}
